import http from './http';
const register = (email, password) => async ({ commit, }) => {
    commit({ type: 'loading' });
    try {
        const user = await http.post('/register', {
            email,
            password,
        });
        commit({ type: 'set-user', payload: user });
    }
    catch (err) {
        commit({ type: 'error', payload: err.message });
    }
};
export default register;
